<template>
  <v-card class="mt-4" dark>
    <v-card-title>
      Recebimentos
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Pesquisar"
        single-line
        hide-details
      ></v-text-field>
      <v-btn fab small color="primary ml-4" dark v-on:click="add()" title="Receber Cesta Básica">
        <i class="material-icons">add</i>
      </v-btn>
    </v-card-title>
    <v-data-table :headers="headers" :items="recebimentos" :items-per-page="5" dark>
      <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="update(item.cdRecebimento)">edit</v-icon>
      <v-icon small @click="remove(item.cdRecebimento)">delete</v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
      <template v-slot:item.intencao.dtIntencao="{item}">
        {{ moment(item.intencao.dtIntencao) }}
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import DataService from '../service/RecebimentoDataService';
import moment from 'moment';

export default {
  data() {
    return {
      search: '',
      recebimentos: [],
      headers: [
        { text: '#', align: 'start', sortable: false, value: 'cdRecebimento'},
        { text: 'Doador', value: 'intencao.doador.nome'},
        { text: 'Data', align: 'center', value: 'intencao.dtIntencao' },
        { text: 'Qtd de Cestas', align: 'center', value: 'qtdCestas' },
        { text: 'Tipo de Cesta', align: 'center', value: 'intencao.tpCesta.nome' },
        { text: 'Status', align: 'center', value: 'intencao.status' },
        { text: 'Ações', align: 'center', value: 'actions', sortable: false },
      ],
    }
  },
  computed: {
    //...mapState({})
  },
  methods: {
    initialize() {},
    moment(date) {
      return moment(date).format('DD/MM/yyyy');
    },
    refresh() {
        DataService.retrieveAll().then((response) => {
            this.recebimentos = response.data;
        });
    },
    add() {
        this.$router.push(`/recebimentos/add`);
    },
    update(id) {
        this.$router.push(`/recebimentos/${id}`);
    },
    remove(id) {
        DataService.remove(id)
        .then(() => {
          this.refresh();
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
    },
  },
  created() {
      this.refresh();
  }
}
</script>

